'use client';

import { useEffect, useRef } from 'react';
import 'leaflet/dist/leaflet.css';
import L from 'leaflet';

// Add type declaration for ymaps global
declare global {
  interface Window {
    ymaps: any;
  }
}

interface MapProps {
  onMapClick: (coords: [number, number]) => void;
  onMarkerDrag: (type: 'departure' | 'destination', coords: [number, number]) => void;
  departureCoords?: [number, number];
  destinationCoords?: [number, number];
}

export default function Map({ onMapClick, onMarkerDrag, departureCoords, destinationCoords }: MapProps) {
  const mapRef = useRef<L.Map | null>(null);
  const departureMarkerRef = useRef<L.Marker | null>(null);
  const destinationMarkerRef = useRef<L.Marker | null>(null);
  const routeRef = useRef<L.Polyline | null>(null);

  useEffect(() => {
    // Initialize map
    if (!mapRef.current) {
      mapRef.current = L.map('map').setView([55.76, 37.64], 10);
      L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
        attribution: '© OpenStreetMap contributors'
      }).addTo(mapRef.current);

      // Add click handler
      mapRef.current.on('click', (e: L.LeafletMouseEvent) => {
        const { lat, lng } = e.latlng;
        onMapClick([lat, lng]);
      });
    }

    // Cleanup on unmount
    return () => {
      if (mapRef.current) {
        mapRef.current.remove();
        mapRef.current = null;
      }
    };
  }, []);

  useEffect(() => {
    if (!mapRef.current) return;

    // Update departure marker
    if (departureCoords) {
      if (departureMarkerRef.current) {
        departureMarkerRef.current.setLatLng(departureCoords);
      } else {
        departureMarkerRef.current = L.marker(departureCoords, {
          draggable: true,
          icon: L.divIcon({
            className: 'bg-blue-500 w-6 h-6 rounded-full border-2 border-white shadow-lg flex items-center justify-center text-white font-bold',
            html: 'A'
          })
        }).addTo(mapRef.current);

        departureMarkerRef.current.on('dragend', (e) => {
          const marker = e.target;
          const position = marker.getLatLng();
          onMarkerDrag('departure', [position.lat, position.lng]);
        });
      }
    } else if (departureMarkerRef.current) {
      departureMarkerRef.current.remove();
      departureMarkerRef.current = null;
    }

    // Update destination marker
    if (destinationCoords) {
      if (destinationMarkerRef.current) {
        destinationMarkerRef.current.setLatLng(destinationCoords);
      } else {
        destinationMarkerRef.current = L.marker(destinationCoords, {
          draggable: true,
          icon: L.divIcon({
            className: 'bg-red-500 w-6 h-6 rounded-full border-2 border-white shadow-lg flex items-center justify-center text-white font-bold',
            html: 'B'
          })
        }).addTo(mapRef.current);

        destinationMarkerRef.current.on('dragend', (e) => {
          const marker = e.target;
          const position = marker.getLatLng();
          onMarkerDrag('destination', [position.lat, position.lng]);
        });
      }
    } else if (destinationMarkerRef.current) {
      destinationMarkerRef.current.remove();
      destinationMarkerRef.current = null;
    }

    // Update route
    if (departureCoords && destinationCoords) {
      if (routeRef.current) {
        routeRef.current.setLatLngs([departureCoords, destinationCoords]);
      } else {
        routeRef.current = L.polyline([departureCoords, destinationCoords], {
          color: '#3B82F6',
          weight: 3,
          opacity: 0.8
        }).addTo(mapRef.current);
      }

      // Fit bounds to show both markers
      const bounds = L.latLngBounds([departureCoords, destinationCoords]);
      mapRef.current.fitBounds(bounds, { padding: [50, 50] });
    } else if (routeRef.current) {
      routeRef.current.remove();
      routeRef.current = null;
    }
  }, [departureCoords, destinationCoords]);

  return <div id="map" className="w-full h-full rounded-lg" />;
} 